<template>
    <div>
        <video :src="mvlist.brs[a]" controls autoplay="autoplay" class="mvbf"></video>
        <div class="mvxx">
            <div class="qingxidu">
                <el-button @click="qixidu(240)">清晰</el-button>
                <el-button @click="qixidu(480)">标清</el-button>
                <el-button @click="qixidu(720)">高清</el-button>
                <el-button @click="qixidu(1080)">超清</el-button>
            </div>
            <div class="mvxinxi">
                <h3 style="text-align: center;padding: 20px;">{{mvlist.name}}</h3>
                <p>
                    <span style="font-weight: bold;">制作人员:</span>
                    <span v-for="art in mvlist.artists" :key="art.id" style="padding-left: 30px;">{{art.name}}</span>
                </p>
                <p>
                    <span style="font-weight: bold;">MV简介:</span>
                    <span style="padding-left: 30px;">{{jj}}</span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "mvbofang",
        data(){
          return{
              a: 720,
              jj:""
          }
        },
        props:{
            mvlist:Object
        },
        created(){
          if (this.mvlist.desc == ""){
              this.jj = "暂无简介"
          }else {
              this.jj = this.mvlist.desc
          }
        },
        methods:{
            qixidu(a){
                this.a = a
            }
        }
    }
</script>

<style scoped>
.mvbf{
    width: 100%;
    height: auto;
}
    .mvxx{
        width: 100%;
        text-align: left;
    }
    .qingxidu{
        margin: 30px;
        display: flex;
        justify-content: space-around;
    }
    .mvxinxi{
        width: 100%;
        height: 500px;
        background: white;
    }
.mvxinxi>p{
    padding-left: 20px;
}
</style>
